.contactsBlock{
    background-color: #dcc9bf;
    height: 100vh;
}

.container{
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.contactsForm{
    display: flex;
    gap: 20px;
    align-items: center;
    width: 300px;
    height: 300px;
    flex-direction: column;
}

.textarea{
    width: 300px;
    height: 150px;
}

.input{
    width: 300px;
    height: 30px;
}
.send{
    background: white;
}

.title{
    background: white;
}